<template>
  <v-app dark class="text-xs-center">
    <div class="bg-image"></div>
    <v-container>
      <div class="card-wrapper">
      <v-card class="pa-4  card">

        <h1 class="pb-4 font-weight-thin">" {{ quote.msg }} "</h1>
        <hr class="ma-4">
        <p class="pt-4">{{ quote.author }}</p>
      </v-card>
      <v-btn color="info" v-on:click="changeQuote">New Quote</v-btn>
      </div>
    </v-container>
  </v-app>

</template>

<script>
import axios from 'axios'

export default {
  data () {
    return {
      quote: {
        msg: '',
        author: ''
      }
    }
  },
  methods: {
    changeQuote () {
      axios.get('https://talaikis.com/api/quotes/random/').then((res) => {
        this.quote.author = res.data.author
        this.quote.msg = res.data.quote
      })
    }
  },
  beforeCreate () {
    axios.get('https://talaikis.com/api/quotes/random/').then((res) => {
      console.log(res.data)
      this.quote.author = res.data.author
      this.quote.msg = res.data.quote
    })
  }
}

</script>

<style scoped>
.card {
  width: 60%;
  margin: auto;

  background: rgba(27, 7, 7, 0.74);
  z-index: 2;
}
.card-wrapper {
  position: relative;
  top: 50%;
  transform: translate(0, -50%);
}
.bg-image {
  background-image: url('https://source.unsplash.com/collection/1163637/1480x1480');
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  position: absolute;
  background-blend-mode: darken;
  filter: blur(10px);
  height: 100%;
  width: 100%;
}
</style>



